import React from "react"
import Helmet from "react-helmet"

function SEO(props) {
  return (
    <Helmet>
      <html lang="en" />
      <title>{props.title}</title>
      <meta name="title" content={props.title} />
      <meta name="description" content={props.description} />

      {/* <!-- Open Graph / Facebook --> */}
      <meta property="og:type" content="website" />
      <meta property="og:url" content="https://cleaning-service.vercel.app" />
      <meta property="og:title" content="Cleaning Service" />
      <meta
        property="og:description"
        content="We Are A Full Year-Round Building Cleaning Service Group"
      />
      <meta property="og:image" content="/static/media-card.jpg" />

      {/* <!-- Twitter --> */}
      <meta property="twitter:card" content="summary_large_image" />
      <meta
        property="twitter:url"
        content="https://cleaning-service.vercel.app"
      />
      <meta property="twitter:title" content="Cleaning Service" />
      <meta
        property="twitter:description"
        content="We Are A Full Year-Round Building Cleaning Service Group"
      />
      <meta property="twitter:image" content="/static/media-card.jpg" />
    </Helmet>
  )
}

export default SEO
