import React from "react"
import { Link, graphql } from "gatsby"

import BackgroundImage from "gatsby-background-image"

import SEO from "./../components/seo"
import Layout from "./../components/layout"
import "./index.scss"

const HomePage = ({ data }) => {
  return (
    <BackgroundImage
      className="hero-wrapper"
      fluid={data.file.childImageSharp.fluid}
      backgroundColor="#FBFBFB"
      backgroundSize="cover"
      backgroundPosition="center"
    >
      <Layout>
        {
          <SEO
            title="Cleaning Service"
            description="We Are A Full Year-Round Building Cleaning Service Group"
          />
        }
        <div className="hero-content">
          <div>
            <h1 className="hero-title">
                We Are A Full Year-Round Building Cleaning Service Group
            </h1>

            <p className="hero-description-bold">
            We are the right choice when it comes to building cleaning service.
            </p>

            <br />
            <p className="hero-description">
            We provide cleaning service for individualis and big corporations
            </p>
            <p className="hero-description">
            We provide full customer cleaning service delivery to your place of business!
            </p>
          </div>
          <div className="hero-img-panel"></div>
        </div>
        <Link to="/par-mums" className="hero-btn first">
          Learn More
        </Link>
        <Link href="mailto:wkirk777@aol.com" className="hero-btn">
          Contact Us 
        </Link>        
      </Layout>
    </BackgroundImage>
  )
}

export const query = graphql`
  {
    file(relativePath: { eq: "hero2.jpg" }) {
      childImageSharp {
        fluid(maxWidth: 5760, quality: 100) {
          ...GatsbyImageSharpFluid_withWebp
          ...GatsbyImageSharpFluidLimitPresentationSize
        }
      }
    }
  }
`

export default HomePage
